.storycard {
    max-width: 438px;
    height: 100%;
}

.story-heading p {
    font-size: 40px;
    font-weight: 600;
    padding-left: 26px;
    padding-right: 25px;
    line-height: 1.2em;
}

.story-content p {
    text-align: left;
    padding-left: 26px;
    padding-right: 26px;
    font-size: 17px;
    letter-spacing: 0px;
    color: #908D8D;
    opacity: 1;
    font-weight: 400;
    line-height: 1.2em;
}
.stories-card .card2details{
    padding-left: 26px;
    padding-right: 26px;
}
@media only screen and (max-width: 786px){
    .story-heading p {
    font-size:19px;
    }
    .story-content p {
        font-size:14px;
    }
    .stories-card .card2details{
        font-size:12px;
    }
}